new WOW().init();

if (!$('html').is('.mobile') && !$('html').is('.tablet') ) {
    $(".button").hover3d({
        selector: "> i",
        perspective: 100
    });

    $(".vacancies__item").hover3d({
        selector: ".vacancies__card"
    });

    $(".projects-slider__nav").hover3d({
        selector: ".projects-slider__nav-wrap",
        perspective: 70
    });

    if (window.navigator.userAgent.indexOf("MSIE ") > 0 || window.navigator.userAgent.indexOf("Edge") > 0) {} else{
        $('.vacancies__item').on("mousemove",function(e) {
            var ax = -($(this).innerWidth()/2- e.offsetX)/20;
            var ay = ($(this).innerHeight()/2- e.offsetY)/10;
            $(this).find('.vacancies__card-img-pattern').attr("style", "transform: translate("+ax/10+"%,"+ay/10+"%)");
            //$(this).find('.vacancies__card-img img:nth-child(2)').attr("style", "transform: scale(1.1) translate("+ax/10+"%,"+ay/10+"%)");
            //$(this).find('.vacancies__card-img-bg').css("background-position", (50 - ax) + "% " + (50 - ay) + "%");
        }).on("mouseout",function(e) {
            $(this).find('.vacancies__card-img-pattern').attr("style", "");
            //$(this).find('.vacancies__card-img img:nth-child(2)').attr("style", "");
            //$(this).find('.vacancies__card-img-bg').css("background-position", "50% 50%");
        });
    }

    $('.js-share').hover3d({
        selector: "> i",
        perspective: 10
    });

    $('.js-vacancy-citation-image').hover3d({
        selector: "> img",
        perspective: 200,
        sensitivity: 100
    });
}

