// шэрилки
$('.js-share').click(function(e) {
    e.preventDefault();
    share[$(this).data('share')]();
});


// обработка меню
let $toggler = $('.js-menu-toggler');
$toggler.on('click', function () {
    $('html').toggleClass('menu-opened', $(this).find('input').prop('checked'));
});

$('.menu__links').on('click', 'a', function() {
    $('.js-menu-toggler input').prop('checked', false);
    $('html').toggleClass('menu-opened', $('.js-menu-toggler input').prop('checked'));
});


// темизация меню в зависимости от блока под ним
function sectionVisible(elem) {
    let coords = elem.getBoundingClientRect();

    //let windowHeight = document.documentElement.clientHeight;
    // верхняя граница elem в пределах видимости ИЛИ нижняя граница видима
    //let topVisible = coords.top > 0 && coords.top < windowHeight;
    //let bottomVisible = coords.bottom < windowHeight && coords.bottom > 0;
    //return topVisible || bottomVisible;

    let val = coords.height + coords.top;
    return val >= 60 && val <= coords.height + 60;
}
function menuTheme() {
    //let sections = document.querySelectorAll('.js-section');
    let sections = document.querySelectorAll('[data-menu]');
    for (let i = sections.length-1; i >= 0; i--) {
        if (sectionVisible(sections[i])) {
            //$('.js-header').toggleClass('_top', i == 0);
            $('.js-header').attr('data-theme', $(sections[i]).data('menu'));
            break;
        }
    }
    //$('.js-header').toggleClass('_fixed', $(window).scrollTop() >= 100);
    $('.js-header').toggleClass('_top', $(window).scrollTop() < 50);
}
$(window).on('scroll', function () {
    menuTheme();
});
menuTheme();

$('.js-logo').click(function(e) {
    if ($('.js-header').is('._top') && $(e.currentTarget).is('[data-redirect]')) {
        e.preventDefault();
        window.location.href = $(e.currentTarget).data('redirect');
    } else {
        anchorClick(e);
    }
});


// отложенная загрузка картинок после загрузки картинок в html
$('body').imagesLoaded( { background: true }, function() {
    $('img[data-src]').each(function() {
        // для блока "гордимся пректами" заменяем катринки для вертикально ориентированного планшета
        if ($(this).is('[data-src-tablet]') && device.tablet() && device.portrait()) {
            $(this).attr('src', $(this).data('src-tablet'));
        } else {
            $(this).attr('src', $(this).data('src'));
        }
    });
});


// для блока "гордимся пректами" заменяем катринки для вертикально ориентированного планшета
$(window).on('orientation:portrait', function() {
    $('img[data-src]').each(function() {
        $(this).attr('src', $(this).data('src-tablet'));
    });
});
$(window).on('orientation:landscape', function() {
    $('img[data-src]').each(function() {
        $(this).attr('src', $(this).data('src'));
    });
});



